export async function getServerSideProps() {
  try {
    const res = await fetch("http://localhost:4000/products");
    if (!res.ok) {
      throw new Error("Failed to fetch products");
    }
    const data = await res.json();

    return {
      props: {
        products: data || [],
      },
    };
  } catch (error) {
    console.error("Error fetching products:", error);
    return {
      props: {
        products: [],
      },
    };
  }
}


